<template>
    <view class="container">
        <!-- 头部 -->
        <view class="image-box">
            <image :src="info.dealBrief.imageUrl" mode="aspectFill"></image>
            <view class="info-box">
                <view class="cinema-name line-ellipsis">{{ cinemaName }}</view>
                <view class="snack-title line-ellipsis">{{ info.dealBrief.title }}</view>
            </view>
            <view class="mask"></view>
        </view>
        <!-- 价格 -->
        <view class="section">
            <view class="price-box">
                <view>
                    <text class="price-num" v-if="info.dealBrief.originPrice">
                        {{ info.dealBrief.originPrice }}
                        <text class="yuan">元</text>
                    </text>
                    <text class="cinema-price" v-if="info.dealBrief.value">影院价:{{ info.dealBrief.value }}元</text>
                </view>
                <view class="buy-btn" @tap="buySnack">立即抢购</view>
            </view>
            <view class="price-box">
                <view class="deal-tags-list">
                    <text class="item" v-if="info.dealBrief.refundTag.supportTimeRefund">
                        <text class="text-icon"></text>
                        随时退
                    </text>
                    <text class="item" v-if="info.dealBrief.refundTag.supportExpireRefund">
                        <text class="text-icon"></text>
                        过期退
                    </text>
                </view>
                <view class="sold">
                    <text class="iconfont icon-04"></text>
                    <text>{{ info.dealBrief.curNumberDesc }}</text>
                </view>
            </view>
        </view>
        <!-- 套餐详情 -->
        <view class="section">
            <view class="section-title">套餐详情</view>
            <view class="dealsets-details" v-for="(item, index) in info.dealBrief.menus" :key="index">
                <view class="set-title" v-if="item.title">{{ item.title }}</view>

                <view class="detail-group" v-for="(item, index1) in item.items" :key="index1">
                    <text class="right line-ellipsis">{{ item.name }}</text>

                    <text class="center line-ellipsis">{{ item.amount }}</text>

                    <text class="left">¥{{ item.subtotal }}</text>
                </view>
            </view>
        </view>
        <!-- 购买须知 -->
        <view class="purchase-notice section">
            <view class="section-title">购买须知</view>
            <view class="terms">
                <view class="terms-item" v-for="(item, index) in info.dealBrief.terms" :key="index">
                    <view class="tip-title" v-if="item.title">{{ item.title }}:</view>

                    <view class="tip-des">{{ item.content }}</view>
                </view>
            </view>
        </view>
        <!-- 影院信息 -->
        <view class="section">
            <view class="section-title">影院信息</view>
            <cinemaMap compName="cinemaMap" :data="{ cinemaData }"></cinemaMap>
        </view>
    </view>
</template>

<script>
import cinemaMap from '../../../templates/cinemaMap/cinemaMap.vue';
export default {
    components: {
        cinemaMap
    },
    data() {
        return {
            info: null,

            //小吃详情
            cinemaName: '',

            //影院地图详情
            cinemaData: null,

            cinemaId: '',
            imageUrl: '',
            title: '',
            originPrice: '',
            value: '',

            refundTag: {
                supportTimeRefund: '',
                supportExpireRefund: ''
            },

            curNumberDesc: '',
            menus: [],
            terms: []
        };
    },
    onLoad(obj) {
        const paramsObj = JSON.parse(obj.paramsStr);
        this.initPage(paramsObj);
    },
    methods: {
        initPage(obj) {
            uni.showLoading({
                title: '正在加载'
            });
            const data = {
                dealId: obj.dealId,
                quantity: 1,
                cinemaId: obj.cinemaId
            };
            uni.request({
                url: 'https://m.maoyan.com/deal/goods/price?_v_=yes&token=_Rpc-H8U5JCblI4hGuJyrBtkd1cAAAAAdQYAADhpD2UCdaExkvgi4bFJcnWhlfI6rc7ilxZE_SENva6l8EU_L8_hQE-hXUB7l21d1w',
                method: 'POST',
                data: data,
                success: (res) => {
                    uni.hideLoading();
                    this.setData({
                        cinemaName: obj.cinemaName,
                        cinemaId: obj.cinemaId,
                        cinemaData: obj.cinemaData,
                        info: res.data.data
                    });
                }
            });
        },

        //跳转到“提交订单”页面
        buySnack() {
            const { info, cinemaName, cinemaId } = this;
            //添加订单信息
            const paramsStr = JSON.stringify({
                cinemaName,
                cinemaId,
                title: info.dealBrief.title,
                //套餐名
                img: info.dealBrief.imageUrl,
                //图片
                amount: 1,
                //数量
                price: info.dealBrief.originPrice,
                //单价
                total: info.dealBrief.originPrice * 1 //合计
            });

            uni.navigateTo({
                url: `/pages/subPages/buy-snack/buy-snack?paramsStr=${paramsStr}`
            });
        }
    }
};
</script>
